import React, { useState, useEffect } from 'react';
import * as XLSX from 'xlsx';
import { saveAs } from 'file-saver';
import jsPDF from 'jspdf';
import autoTable from 'jspdf-autotable';



function App() {
  const [form, setForm] = useState({
    fecha: '',
    tipo: 'ingreso',
    categoria: '',
    descripcion: '',
    monto: '',
    metodo_pago: ''
  });

  const [movimientos, setMovimientos] = useState([]);
  const [editId, setEditId] = useState(null);
  const [categoriaBusqueda, setCategoriaBusqueda] = useState('');
  const [filtroActivo, setFiltroActivo] = useState('');


  const handleChange = (e) => {
    setForm({ ...form, [e.target.name]: e.target.value });
  };

  const handleSubmit = async (e) => {
    e.preventDefault();

    const url = editId
      ? `http://localhost:3001/api/movimientos/${editId}`
      : 'http://localhost:3001/api/movimientos';

    const method = editId ? 'PUT' : 'POST';

    await fetch(url, {
      method,
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify(form),
    });

    setForm({
      fecha: '',
      tipo: 'ingreso',
      categoria: '',
      descripcion: '',
      monto: '',
      metodo_pago: ''
    });
    setEditId(null);
    cargarMovimientos();
  };

  

  const eliminarMovimiento = async (id) => {
    if (!window.confirm('Eliminar este movimiento?')) return;

    await fetch(`http://localhost:3001/api/movimientos/${id}`, {
      method: 'DELETE',
    });

    cargarMovimientos();
  };

  const comenzarEdicion = (movimiento) => {
    setForm({
      fecha: movimiento.fecha.slice(0, 10),
      tipo: movimiento.tipo,
      categoria: movimiento.categoria,
      descripcion: movimiento.descripcion,
      monto: movimiento.monto,
      metodo_pago: movimiento.metodo_pago
    });
    setEditId(movimiento.id);
  };

  const cargarMovimientos = async () => {
    const res = await fetch('http://localhost:3001/api/movimientos');
    const data = await res.json();
    setMovimientos(data);
  };

  
//  PEG AC 
const exportarExcel = () => {
  const hoja = movimientos.map(m => ({
    Fecha: new Date(m.fecha).toLocaleDateString('es-AR'),
    Tipo: m.tipo.toUpperCase(),
    Categora: m.categoria,
    Descripcin: m.descripcion,
    Monto: Number(m.monto),
    Mtodo: m.metodo_pago
  }));

  const ws = XLSX.utils.json_to_sheet(hoja);
  const wb = XLSX.utils.book_new();
  XLSX.utils.book_append_sheet(wb, ws, 'Movimientos');

  const excelBuffer = XLSX.write(wb, { bookType: 'xlsx', type: 'array' });
  const blob = new Blob([excelBuffer], { type: 'application/octet-stream' });
  const fechaHoy = new Date().toISOString().slice(0, 10);
  saveAs(blob, `movimientos_${fechaHoy}.xlsx`);
  
};

const exportarPDF = () => {
  const doc = new jsPDF();
  doc.text('Lista de Movimientos', 14, 15);

  const tabla = movimientos.map(m => [
    new Date(m.fecha).toLocaleDateString('es-AR'),
    m.tipo.toUpperCase(),
    m.categoria,
    m.descripcion,
    `$${Number(m.monto).toLocaleString('es-AR')}`,
    m.metodo_pago
  ]);

  autoTable(doc, {
    head: [['Fecha', 'Tipo', 'Categora', 'Descripcin', 'Monto', 'Mtodo']],
    body: tabla,
    startY: 20
  });
  
  const fechaHoy = new Date().toISOString().slice(0, 10);
  doc.save(`movimientos_${fechaHoy}.pdf`);
};


  useEffect(() => {
    cargarMovimientos();
  }, []);

  const totalIngresos = movimientos
    .filter(m => m.tipo === 'ingreso')
    .reduce((acc, m) => acc + Number(m.monto), 0);

  const totalEgresos = movimientos
    .filter(m => m.tipo === 'egreso')
    .reduce((acc, m) => acc + Number(m.monto), 0);

  const saldo = totalIngresos - totalEgresos;
  const movimientosFiltrados = movimientos.filter(m =>
    m.categoria.toLowerCase().includes(categoriaBusqueda.toLowerCase())
  );
  
  return (
    <div className="container-fluid px-3 py-4">

      <h2 className="text-center mb-4 text-primary"> Registro de Movimientos</h2>

      <form onSubmit={handleSubmit} className="card p-4 shadow-sm mb-5">
        <div className="row g-3">
          <div className="col-md-4">
            <label className="form-label">Fecha</label>
            <input type="date" className="form-control" name="fecha" value={form.fecha} onChange={handleChange} required />
          </div>
          <div className="col-md-4">
            <label className="form-label">Tipo</label>
            <select className="form-select" name="tipo" value={form.tipo} onChange={handleChange}>
              <option value="ingreso">Ingreso</option>
              <option value="egreso">Egreso</option>
            </select>
          </div>
          <div className="col-md-4">
            <label className="form-label">Categora</label>
            <input type="text" className="form-control" name="categoria" value={form.categoria} onChange={handleChange} required />
          </div>
          <div className="col-md-6">
            <label className="form-label">Descripcin</label>
            <input type="text" className="form-control" name="descripcion" value={form.descripcion} onChange={handleChange} />
          </div>
          <div className="col-md-3">
            <label className="form-label">Monto</label>
            <input type="number" className="form-control" name="monto" value={form.monto} onChange={handleChange} required />
          </div>
          <div className="col-md-3">
            <label className="form-label">Mtodo de Pago</label>
            <input type="text" className="form-control" name="metodo_pago" value={form.metodo_pago} onChange={handleChange} />
          </div>
        </div>

        <div className="text-end mt-3">
          {editId ? (
            <>
              <span className="text-warning me-3"> Editando movimiento</span>
              <button type="submit" className="btn btn-warning">Actualizar</button>
              <button
                type="button"
                className="btn btn-secondary ms-2"
                onClick={() => {
                  setEditId(null);
                  setForm({
                    fecha: '',
                    tipo: 'ingreso',
                    categoria: '',
                    descripcion: '',
                    monto: '',
                    metodo_pago: ''
                  });
                }}
              >
                Cancelar
              </button>
            </>
          ) : (
            <button className="btn btn-success w-100">Guardar</button>

          )}
        </div>
      </form>

      <div className="mb-4 row">
  <div className="col-md-6 col-sm-8">
    <input
      type="text"
      className="form-control"
      placeholder=" Buscar por categora"
      value={categoriaBusqueda}
      onChange={(e) => setCategoriaBusqueda(e.target.value)}
    />
  </div>
  <div className="col-md-6 col-sm-4 d-flex gap-2 mt-2 mt-sm-0">
    <button className="btn btn-primary" onClick={() => setFiltroActivo(categoriaBusqueda)}>
      Buscar
    </button>
    <button className="btn btn-secondary" onClick={() => {
      setCategoriaBusqueda('');
      setFiltroActivo('');
    }}>
      Limpiar
    </button>
  </div>
</div>


      <h3 className="text-center mb-3"> Lista de Movimientos</h3>

      

{/*  Vista tarjeta para mviles */}
<div className="d-md-none">
  {movimientosFiltrados.map((m) => (

    <div key={m.id} className={`card mb-3 shadow-sm border-${m.tipo === 'ingreso' ? 'success' : 'danger'}`}>
      <div className="card-body">
        <h5 className="card-title mb-2">
          {new Date(m.fecha).toLocaleDateString('es-AR')} - <strong className={m.tipo === 'ingreso' ? 'text-success' : 'text-danger'}>{m.tipo.toUpperCase()}</strong>
        </h5>
        <p className="mb-1"><strong>Categora:</strong> {m.categoria}</p>
        <p className="mb-1"><strong>Descripcin:</strong> {m.descripcion}</p>
        <p className="mb-1"><strong>Monto:</strong> ${Number(m.monto).toLocaleString('es-AR')}</p>
        <p className="mb-2"><strong>Mtodo:</strong> {m.metodo_pago}</p>
        <div className="d-flex justify-content-end gap-2">
          <button className="btn btn-sm btn-warning" onClick={() => comenzarEdicion(m)}></button>
          <button className="btn btn-sm btn-danger" onClick={() => eliminarMovimiento(m.id)}></button>
        </div>
      </div>
    </div>
  ))}
</div>

{/*  Vista de tabla para pantallas grandes */}
<div className="table-responsive d-none d-md-block">
  <table className="table table-bordered table-striped align-middle">
    <thead className="table-light">
      <tr>
        <th>Fecha</th>
        <th>Tipo</th>
        <th>Categora</th>
        <th>Descripcin</th>
        <th>Monto</th>
        <th>Mtodo</th>
        <th>Accin</th>
      </tr>
    </thead>
    <tbody>
      {movimientosFiltrados.map((m) => (

        <tr key={m.id} className={m.tipo === 'ingreso' ? 'table-success' : 'table-danger'}>
          <td>{new Date(m.fecha).toLocaleDateString('es-AR')}</td>
          <td><strong>{m.tipo.toUpperCase()}</strong></td>
          <td>{m.categoria}</td>
          <td>{m.descripcion}</td>
          <td>${Number(m.monto).toLocaleString('es-AR')}</td>
          <td>{m.metodo_pago}</td>
          <td className="text-nowrap">
            <button className="btn btn-sm btn-warning me-2" onClick={() => comenzarEdicion(m)}></button>
            <button className="btn btn-sm btn-danger" onClick={() => eliminarMovimiento(m.id)}></button>
          </td>
        </tr>
      ))}
    </tbody>
  </table>
</div>

<div className="d-flex justify-content-end gap-2 mb-3">
  <button className="btn btn-outline-success" onClick={exportarExcel}> Exportar Excel</button>
  <button className="btn btn-outline-danger" onClick={exportarPDF}> Exportar PDF</button>
</div>


      <div className="card p-4 mt-4">
        <h4> Resumen</h4>
        <ul className="list-group list-group-flush">
          <li className="list-group-item">Total Ingresos: <strong className="text-success">${totalIngresos.toLocaleString('es-AR')}</strong></li>
          <li className="list-group-item">Total Egresos: <strong className="text-danger">${totalEgresos.toLocaleString('es-AR')}</strong></li>
          <li className="list-group-item">Saldo: <strong className="text-dark">${saldo.toLocaleString('es-AR')}</strong></li>
        </ul>
      </div>
    </div>
  );
}

export default App;
